import React from 'react'
import { Row, Col } from 'antd';
class Contact extends React.Component{
    render(){
        return(
          <div>
          <Row gutter={24} style={{textAlign:'justify'}}>
          <Col span={8} />
          <Col span={8} >
          <h1>Contacto</h1>
            <p>
            Es un hecho establecido hace demasiado tiempo que un lector se distraer con el contenido del texto de un sitio mientras que mira su diseo. El punto de usar Lorem Ipsum es que tiene una distribucin ms o menos normal de las letras, al contrario de usar textos como por ejemplo "Contenido aqu, contenido aqu". Estos textos hacen parecerlo un espaol que se puede leer. Muchos paquetes de autoedicin y editores de pginas web usan el Lorem Ipsum como su texto por defecto, y al hacer una bsqueda de "Lorem Ipsum" va a dar por resultado muchos sitios web que usan este texto si se encuentran en estado de desarrollo. Muchas versiones han evolucionado a travs de los aos, algunas veces por accidente, otras veces a propsito (por ejemplo insertndole humor y cosas por el estilo).

            </p>
          </Col>
          <Col span={8} />
        </Row>
          </div>
        )
    }
}

export default Contact;



